.shows-wrapper {
  padding: 3rem 6rem;
  background: #f7f8f9;
  min-height: 100vh;
  color: #111;
}

/* Center content and constrain width for better readability */
.shows-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.movie-title {
  font-size: 3.2rem;
  font-weight: 800;
  margin: 1rem 0 1.5rem 0;
  letter-spacing: -0.5px;
}

.date-strip {
  display: flex;
  align-items: center;
  gap: 1rem;
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
  padding: 1rem 0;
}

.date-box {
  text-align: center;
  font-weight: 700;
  width: 110px;
  padding: 0.25rem 0.5rem;
}

/* highlight today's date visually (you can add logic to mark today with class 'today') */
.date-box.today {
  border-bottom: 3px solid #0b6b3a;
  background: rgba(11,107,58,0.04);
  border-radius: 6px;
}

.date-box.selected {
  background: rgba(11,107,58,0.06);
  border-radius: 6px;
  box-shadow: 0 2px 8px rgba(11,107,58,0.06);
}

.day {
  display: block;
  font-size: 0.95rem;
  color: #222;
}

.date {
  font-size: 0.85rem;
  color: #555;
  margin-top: 0.25rem;
}

.lang-box {
  margin-left: auto;
  font-weight: 700;
  font-size: 1rem;
  color: #111;
}

.subtitle-info {
  margin-top: 1.2rem;
  color: #666;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  gap: 0.6rem;
}

.legend {
  display: flex;
  gap: 2rem;
  margin-top: 1.2rem;
  font-size: 0.95rem;
}

.legend .dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: inline-block;
  margin-right: 8px;
}

.dot.available { background: #1db954; }
.dot.not { background: #e02424; }
.dot.fast { background: #f5b400; }

/* Theaters */
.theater-list {
  margin-top: 2.2rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.theater-block {
  background: #eef6f9;
  padding: 1.6rem 1.8rem;
  border-radius: 12px;
  box-shadow: 0 1px 0 rgba(0,0,0,0.03) inset;
}

.theater-name {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

.showtimes {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
}

.showtime-btn {
  border: 0;
  background: #ffffff;
  padding: 0.55rem 1.05rem;
  border-radius: 10px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: transform 0.12s ease, box-shadow 0.12s ease, background 0.12s ease;
  box-shadow: 0 1px 2px rgba(16,24,40,0.04);
}

.showtime-btn.available { color: #0b6b3a; }
.showtime-btn.not { color: #9b1d1d; }
.showtime-btn.fast { color: #b36b00; }

.showtime-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 18px rgba(20,30,45,0.08);
}

.no-shows {
  margin-top: 2rem;
  color: #777;
  text-align: center;
}

/* small screens */
@media (max-width: 900px) {
  .shows-wrapper { padding: 1.2rem 1.2rem; }
  .movie-title { font-size: 2rem; }
  .date-box { width: 70px; }
  .theater-block { padding: 1rem; }
}
